import React from 'react'
import { Community } from './community'

const NoPermission = React.lazy(() =>
  import('./no-permission').then(({ Auth }) => ({ default: Auth }))
)

const Community2 = React.lazy(() =>
  import('./community2').then(({ Community2 }) => ({ default: Community2 }))
)

const Building = React.lazy(() =>
  import('./building').then(({ Building }) => ({ default: Building }))
)

const Room = React.lazy(() =>
  import('./room').then(({ Room }) => ({ default: Room }))
)

const RoomUpdate = React.lazy(() =>
  import('./room-update').then(({ RoomUpdate }) => ({
    default: RoomUpdate
  }))
)

const Search = React.lazy(() =>
  import('./search').then(({ Search }) => ({ default: Search }))
)

export const Pages = {
  NoPermission,
  Community,
  Community2,
  Building,
  Room,
  RoomUpdate,
  Search
}
