import { useMemo } from 'react'
import { useStore } from './use-store'

type Breadcrumb = {
  name: string
  id: number
  level: number
}

export const useBreadcrumb = (communityLevel: number) => {
  const store = useStore()
  const { community, community2, building, room } = store
  return useMemo(() => {
    let list = [community, community2, building, undefined, room]
    // return list.slice(0, communityLevel).filter((item) => item !== undefined)
    list = list.slice(0, communityLevel).filter((item) => item !== undefined)
    if (list.length <= 1) {
      return list
    }
    const parentId = list[list.length - 1]?.parentId
    const parentIndex = list.findIndex((item) => item?.id === parentId)
    list.splice(parentIndex + 1, list.length - 1 - (parentIndex + 1))
    return list
  }, [community, community2, building, room, communityLevel]) as Breadcrumb[]
}
