export class CardUtil {
  public birthday!: string
  public sex!: 0 | 1
  constructor(private idCard: string) {
    this.birthday = this.getBirthday(idCard)
    this.sex = this.getSex(idCard)
  }

  public getAge() {
    const birthDayTime = new Date(this.birthday).getTime()
    // 
    const nowTime = new Date().getTime()
    return Math.ceil((nowTime - birthDayTime) / 31536000000)
  }

  private getBirthday(idCard: string) {
    let birthday = ''
    if (idCard != null && idCard != '') {
      if (idCard.length == 15) {
        birthday = '19' + idCard.substr(6, 6)
      } else if (idCard.length == 18) {
        birthday = idCard.substr(6, 8)
      }
      birthday = birthday.replace(/(.{4})(.{2})/, '$1-$2-')
    }
    return birthday
  }
  private getSex(idCard: string) {
    let sexStr: 0 | 1
    if (parseInt(idCard.slice(-2, -1)) % 2 == 1) {
      sexStr = 1
    } else {
      sexStr = 0
    }
    return sexStr
  }
}

export function arrUnique<T>(arr: T[]) {
  return Array.from(new Set(arr))
}
