import create from 'zustand'
import produce from 'immer'
import type { SelectorOption } from 'antd-mobile/es/components/selector'
import {
  CommunityUserData,
  FormattedCommunityUserData,
  SelectTypeKeys,
  UserBasicInfoKeys,
  UserOtherInfoKeys
} from '@/types'
import { KEYS, UserInfoMap } from '@/constants'
import { service } from '@/services'
import { useEffect, useMemo } from 'react'

export interface UserInfoState {
  basicKeys: UserBasicInfoKeys[]
  otherKeys: UserOtherInfoKeys[]
  selectTypeKeys: SelectTypeKeys[]
  userInfoMap: typeof UserInfoMap
  initialize(): Promise<void>
  users?: FormattedCommunityUserData[]
  addCommunityUser(data: CommunityUserData): Promise<void>
  addCommunityUsers(data: CommunityUserData[]): Promise<void>
  updateCommunityUsers(data: CommunityUserData[]): Promise<void>
  addUser(): void
  reset(): void
  defaultUser(): FormattedCommunityUserData
  deleteUser(roomId: number, index: number): Promise<void>
  getCommunityUsers(
    id: number,
    callback?: (state: UserInfoState) => void
  ): Promise<void>
  getOptions(key: UserBasicInfoKeys | UserOtherInfoKeys): Promise<void>
}

/**
 * Formats form data 
 */
export function formatFormData(
  data: FormattedCommunityUserData
): CommunityUserData {
  return Object.keys(data).reduce((pre, _key) => {
    const key = _key as keyof FormattedCommunityUserData
    // id <= 0  user
    if (key === 'id' && data.id <= 0) {
      data.id = 0
    }
    if (
      key in UserInfoMap &&
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      (UserInfoMap[key]?.selectCode || UserInfoMap[key]?.options) &&
      Array.isArray(data[key])
    ) {
      pre[key] = (data[key] as any)[0]
    } else {
      pre[key] = data[key]
    }
    return pre
  }, Object.create({}))
}
let randomId = 0
export function getDefaultUserInfo(): CommunityUserData {
  return {
    addressId: 0,
    age: '',
    cardNumber: '',
    creator: '',
    educationDegree: '',
    gmtCreate: '',
    gmtModified: '',
    householderRelation: '',
    id: randomId--,
    isDeleted: false,
    isDevelopmentArea: true,
    isDisability: false,
    isNeedyHouseholds: false,
    isOverseasChinese: false,
    isPersonnel: false,
    isRetiredCadre: false,
    maritalStatus: '',
    militaryService: '',
    mobile: '',
    nation: '',
    personName: '',
    politicalOutlook: '',
    profession: '',
    propertyProperties: '',
    registryAddress: '',
    remarks: '',
    sex: ''
  }
}

export const useUserFormStore = create<UserInfoState>((_set, _get) => {
  const set = (
    partial:
      | UserInfoState
      | Partial<UserInfoState>
      | ((state: UserInfoState) => void),
    replace?: boolean | undefined
  ) => {
    if (typeof partial !== 'function') {
      return _set(partial, replace)
    }
    _set(produce(partial))
  }
  const state: UserInfoState = {
    basicKeys: KEYS.basic,
    otherKeys: KEYS.other,
    userInfoMap: UserInfoMap,
    selectTypeKeys: KEYS.selectType,
    users: undefined,
    async initialize() {
      const promises = this.selectTypeKeys.map((key) => {
        return this.getOptions(key).catch(() => {
          //
        })
      })
      await Promise.all(promises)
      // 
      // if (!this.users.length) {
      //   console.log('initialize user')
      //   this.addUser()
      // }
    },
    defaultUser() {
      return formatedUserInfo(getDefaultUserInfo())
    },
    addUser() {
      set((state) => {
        state.users = state.users || []
        state.users.push(this.defaultUser())
      })
    },
    async deleteUser(roomId: number, index: number) {
      const user = this.users?.[index]
      if (user) {
        if (user.id) {
          await service.deleteCommunityUser({
            addressId: roomId,
            personId: user.id
          })
        }
        set((state) => {
          state.users?.splice(index, 1)
        })
      }
    },
    async getOptions(key) {
      const config = this.userInfoMap[key]
      if (!config) {
        throw new Error('config not found with key: ' + key)
      }
      if (
        config.options?.length ||
        !config.selectCode ||
        this.userInfoMap[key].options
      ) {
        return
      }

      const rawOptions = await service.getOptions(config.selectCode)
      const options: Array<SelectorOption<any>> = rawOptions.map((item) => {
        return {
          label: item.dicvalue,
          value: item.dicvalue
        }
      })
      set((state) => {
        if (config.inputType === 'picker') {
          state.userInfoMap[key].options = [options]
        } else {
          state.userInfoMap[key].options = options
        }
      })
    },
    async addCommunityUser(data) {
      service.addCommunityUser(data)
    },

    async addCommunityUsers(data) {
      service.addCommunityUsers(data)
    },

    async updateCommunityUsers(data) {
      service.updateCommunityUsers(data)
    },
    async getCommunityUsers(id: number, callback) {
      if (id === undefined || String(id) === '') {
        return
      }
      const data = await service.getCommunityUsers(id)
      const formatedData = data.map(formatedUserInfo)
      set((state) => {
        state.users = formatedData
        callback?.(state)
      })
    },
    reset() {
      set((state) => {
        state.users = undefined
      })
    }
  }
  return state
})

export const useFormItems = () => {
  const store = useUserFormStore()
  const { basicKeys, otherKeys, userInfoMap } = store
  const basicItems = useMemo(() => {
    return basicKeys.map((key) => {
      return userInfoMap[key]
    })
  }, [basicKeys, userInfoMap])

  const otherItems = useMemo(() => {
    return otherKeys.map((key) => {
      return userInfoMap[key]
    })
  }, [otherKeys, userInfoMap])
  return [basicItems, otherItems] as const
}

export const formatedUserInfo = (
  user: CommunityUserData
): FormattedCommunityUserData => {
  return Object.keys(user).reduce((pre, _key) => {
    const key = _key as keyof CommunityUserData

    if (
      key in UserInfoMap &&
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      (UserInfoMap[key]?.selectCode || UserInfoMap[key]?.options) &&
      !Array.isArray(user[key])
    ) {
      pre[key] = [user[key]]
    } else {
      pre[key] = user[key]
    }
    return pre
  }, Object.create({}))
}

export const useCommunityUsers = (
  id: number | undefined,
  cache?: boolean,
  callback?: (state: UserInfoState) => void
) => {
  const userFormStore = useUserFormStore()
  useEffect(() => {
    if (cache || id == undefined) {
      return
    }
    userFormStore.getCommunityUsers(id, callback)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, cache])
  return userFormStore.users
}
