export const REG_CARD_NUMBER =
  /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/

export const isIdCardValid = (card: string) => {
  return REG_CARD_NUMBER.test(card)
}

/**
 * 
 */
export const REG_MOBILE = /^1[3456789]\d{9}$/

/**
 * 
 */
export const REG_TEL = /^(0\d{2,3})-?(\d{7,8})$/

export const KEYS = {
  basic,
  other,
  selectType
}

