export type RoomData = CommonCommunityData
export type BuildingData = CommonCommunityData

export type CommunityData = CommonCommunityData

class Service {
  // @mock({
  //   type: CommunityType.city,
  //   id: 1,
  //   name: '',
  //   level: 1
  // })
  public getCommunity(id: number) {
    return request.get<Community>('/report/communityAddress/getById', {
      id
    })
  }

  public getCommunityList(params: { id: number; name?: string }) {
    return request.post<CommunityData[]>(
      '/report/communityAddress/list',
      params
    )
  }
  public getCommunityOptions(params: { id: number; name?: string }) {
    return this.getCommunityList(params).then((res) => {
      return res?.map((item) => {
        return {
          id: item.id,
          name: item.name,
          label: item.name,
          value: item.id,
          key: item.id,
          level: item.level,
          collectionStatus: item.toBeCollect
        }
      })
    })
  }

  /**
   * 
   */
  public addCommunityAddress(parentId: number, name: string) {
    return request.post<number>('/report/communityAddress/add', {
      parentId,
      name
    })
  }

  /**
   * 
   */
  public getOptions(code: string) {
    return request.get<AsyncOption[]>('/report/configDict/list', {
      code
    })
  }
  public addCommunityUser(data: CommunityUserData) {
    return request.post<AsyncOption[]>('/report/populationInfo/add', data)
  }

  public deleteCommunityUser(data: { addressId: number; personId: number }) {
    return request.post<AsyncOption[]>('/report/populationInfo/delete', data)
  }

  public updateCommunityUser(data: CommunityUserData) {
    return request.post<AsyncOption[]>('/report/populationInfo/edit', data)
  }

  public addCommunityUsers(data: CommunityUserData[]) {
    return request.post<AsyncOption[]>('/report/populationInfo/addList', data)
  }

  public updateCommunityUsers(data: CommunityUserData[]) {
    return request.post<AsyncOption[]>('/report/populationInfo/editList', data)
  }
  /**
   * 
   */
  public getCommunityUsers(id: number) {
    return request.get<CommunityUserData[]>(
      '/report/communityAddress/listPopulation',
      {
        id
      }
    )
  }

  /**
   * Gets communmity parents ID
   */
  public getCommunmityParents(id: number) {
    return request.get<CommunityData[]>(
      '/report/communityAddress/getParentById',
      {
        id
      }
    )
  }

  /**
   * Gets building detail ID
   */
  public getBuildingDetail(params: { id: number }) {
    return request.get<BuildingData>('/report/communityAddress/getById', params)
  }

  /**
   * Checks id card with detail
   * 0  1 
   *  
   */
  public checkIdCard(params: {
    addressId: number
    cardNumber: string
    personId?: number
  }) {
    return request.post<0 | 1 | CommunityUserData>(
      '/report/populationInfo/isRepeat',
      params,
      {
        loading: false
      }
    )
  }

  public getToken(code: string) {
    return request.post<string>('/report/scanCodeLogin', {
      code,
      utype: 2
    })
  }

  public getUser() {
    return request
      .get<{
        dataPermissionsList: Array<{ id: number; name: string }>
      }>('/report/scanCodeLogin/getUser')
      .then((data) => {
        return data.dataPermissionsList[0]
      })
  }
}

export const service = new Service()
