import axios from 'axios'

import { config } from '@/config'
import type { ToastHandler } from 'antd-mobile/es/components/toast'
import { Toast } from 'antd-mobile'
import { Token } from '@/utils/token'

const service = axios.create({
  baseURL: config.baseURL,
  timeout: 10000,
  headers: {
    // 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
  }
})

const authWhiteList = ['/report/scanCodeLogin']

service.interceptors.request.use((config) => {
  if (config.url) {
    if (authWhiteList.some((white) => config.url === white)) {
      return config
    }
  }
  if (config.headers) {
    const token = Token.getToken()
    if (token) {
      config.headers.AuthToken = token
    }
    // config.headers.AuthToken =
    //   'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyIjp7InVzZXJJZCI6ODUwMTMsImNyYWN0VW5pZCI6IjgwMjcwNyIsIm1vYmlsZSI6IjE1OTg4NDU3NzYzIiwidXNlclN0YXR1cyI6MSwiY3JhY3ROYW1lIjoi6LCi5rSBIiwicm9sZUlkIjoxLCJkZXB0SWQiOjIyLCJkYXRhUGVybWlzc2lvbnNMaXN0IjpbeyJpZCI6MSwibmFtZSI6Iuaeq-WyreekvuWMuiIsImlzUGVybWlzc2lvbiI6dHJ1ZX0seyJpZCI6MiwibmFtZSI6Iue7v-iwt-ekvuWMuiIsImlzUGVybWlzc2lvbiI6dHJ1ZX0seyJpZCI6MywibmFtZSI6IuengOWxseekvuWMuiIsImlzUGVybWlzc2lvbiI6dHJ1ZX1dfSwiZXhwIjoxNjU4MTExMDQ4fQ.ds6R5PMpCIa_PZivx2vZjY2QM930fyFJl-jBUvaciMY'
  }
  return config
})

service.interceptors.response.use(
  (response) => {
    // 
    const res = response.data
    // code0data.
    if (res.code === 2000) {
      return res
    } else if (res.code === 4002) {
      Token.clearToken()
      window.location.reload()
      return res
    } else {
      // service
      // Toast.show(res.msg || '')
      return res
    }
  },
  (err) => {
    toast('')
    throw err
  }
)

function startLoading() {
  return Toast.show({
    icon: 'loading',
    duration: 0
  })
}

type Options = {
  loading?: boolean
  error?: boolean
}

let globalTimer: NodeJS.Timeout
function toast(msg: string, icon?: string) {
  const config = Object.create({})
  config.content = msg
  if (icon) {
    config.icon = icon
  }
  clearTimeout(globalTimer)
  //  Toast 
  globalTimer = setTimeout(() => {
    Toast.show(config)
  }, 500)
}

function _request<R, P = any>(
  url: string,
  method: 'get' | 'post',
  params?: P,
  options: Options = { loading: true, error: true }
) {
  // loading
  let loading: ToastHandler | null = null
  if (options.loading) {
    loading = startLoading()
  }
  return new Promise<R>((resolve, reject) => {
    let data = {}
    // getparams
    if (method === 'get') {
      data = { params }
    }
    // postdata
    if (method === 'post') {
      data = { data: params }
    }

    service({
      url,
      method,
      ...data
    } as any)
      .then((res: any) => {
        if (res.code === 2000) {
          resolve(res.data)
        } else if (res.code === 4002) {
          console.log('')
        } else {
          // 
          if (options.error && res.msg) {
            toast(res.msg)
          }
          reject(res.msg)
        }
      })
      .catch((error) => {
        const errText = error.message || error.msg
        if (errText) {
          toast(errText)
        }
        throw error
      })
      .finally(() => {
        Toast.clear()
        loading && loading.close()
      })
  })
}

function get<R, P = any>(url: string, params?: P, options?: Options) {
  return _request<R, P>(url, 'get', params, options)
}
function post<R, P = any>(url: string, params?: P, options?: Options) {
  return _request<R, P>(url, 'post', params, options)
}

export const request = {
  get,
  post
}
