import create from 'zustand'
import produce from 'immer'
import { useEffect } from 'react'
import { service } from '@/services'
import { Token } from '@/utils/token'
import { useParams } from 'react-router-dom'
import dd from 'gdt-jsapi'
import { CommunityLevel } from '@/types'
interface State {
  /**
   *  
   */
  isAuthed: boolean
  /**
   * app 
   */
  isInitialized: boolean

  /**
   * 
   */
  isInitializedParents: boolean

  community?: Community
  // todo
  community2?: Community2
  building?: IBuilding
  room?: IRoom
  community2List?: Community2[]
  buildings?: IBuilding[]
  rooms?: IRoom[]
  user: User

  initialize: () => void

  /**
   * 
   */
  getCommunity(): Promise<void>
  /**
   * 
   */
  getCommunity2List(): Promise<void>
  /**
   * 
   */
  getBuildings(): Promise<void>
  resetBuildings(): void
  /**
   * 
   */
  getRooms(params: { id: number }): Promise<void>
  resetRooms(): void

  /**
   * 
   */
  setCommunity2(item: Community2): void
  /**
   * 
   */
  setBuilding(item: IBuilding): void
  /**
   * 
   */
  setRoom(item: IRoom): void

  setCommunityByType(item: CommonCommunityData): void

  /**
   * 
   */
  addRoom(parentId: number, name: string): Promise<number>

  /**
   *  communities
   * @param id communityId
   * @param currenntLevel community level
   */
  initCommunities(id: number, currenntLevel: number): Promise<void>

  initCommunityParents(id?: number): Promise<void>
}

export const useStore = create<State>((_set, _get) => {
  const set = (
    partial: State | Partial<State> | ((state: State) => void),
    replace?: boolean | undefined
  ) => {
    if (typeof partial !== 'function') {
      return _set(partial, replace)
    }
    _set(produce(partial))
  }
  const state: State = {
    isAuthed: false,
    isInitialized: false,
    isInitializedParents: false,
    user: {
      name: '',
      communityId: 1
    },
    async initialize() {
      // const isAuth = !!Token.getToken()
      const res = await dd.getAuthCode({})
      const token = await service.getToken(res.code!)
      Token.setToken(token)
      const permission = await service.getUser()

      set((state) => {
        state.user.communityId = permission.id
        state.isInitialized = true
        state.isAuthed = true
      })
    },
    async getCommunity() {
      if (this.community) {
        return
      }
      // const data = await service.getCommunityList(this.user.communityId)
      const data = await service.getCommunity(this.user.communityId)
      set((state) => {
        state.community = data
      })
    },
    async getCommunity2List() {
      if (this.community2List && this.community2List.length) {
        return
      }
      const data = await service.getCommunityList({
        id: this.community!.id
      })
      set((state) => {
        state.community2List = data
      })
    },

    async getBuildings() {
      const data = await service.getCommunityList({
        id: this.community2!.id
      })
      set((state) => {
        state.buildings = data
      })
    },

    resetBuildings() {
      set((state) => {
        state.buildings = undefined
      })
    },

    async getRooms(params) {
      const data = await service.getCommunityList(params)
      set((state) => {
        state.rooms = data
      })
    },
    resetRooms() {
      set((state) => {
        state.rooms = undefined
      })
    },
    setCommunity2(item) {
      set((state) => {
        state.community2 = item
      })
    },
    setBuilding(item) {
      set((state) => {
        state.building = item
      })
    },
    setRoom(item) {
      set((state) => {
        state.room = item
      })
    },
    setCommunityByType(item) {
      switch (item.level) {
        case CommunityLevel.community2:
          this.setCommunity2(item)
          break
        case CommunityLevel.building:
          this.setBuilding(item)
          break
        case CommunityLevel.room:
          this.setRoom(item)
          break
      }
    },
    async addRoom(parentId, name) {
      const res = await service.addCommunityAddress(parentId, name)
      if (res == 2) {
        throw new Error('')
      }
      // 
      this.getRooms({ id: parentId })
      return res
    },
    async initCommunities(_id: number, level: number) {
      if (level === 1) {
        this.getCommunity()
      } else {
        // todo
        // this.getCommunity()
      }
    },
    async initCommunityParents(id) {
      // 
      if (id === this.community?.id || !id) {
        set((state) => {
          state.isInitializedParents = true
        })
        return
      }
      if (this.isInitializedParents) {
        return
      }
      const promises = [
        service.getCommunmityParents(id)
        // service.getBuildingDetail({ id: id as number })
      ] as const
      const communities = await Promise.all(promises).then(([parents]) => {
        return [...parents]
      })
      const newValue: {
        community?: Community
        community2?: Community2
        building?: IBuilding
        room?: IRoom
        isInitializedParents?: boolean
      } = {}
      communities.forEach((community) => {
        const { level } = community
        if (level === CommunityLevel.community) {
          newValue.community = community
        } else if (level === CommunityLevel.community2) {
          newValue.community2 = community
        } else if (level === CommunityLevel.building) {
          newValue.building = community
        } else if (level === CommunityLevel.room) {
          newValue.room = community
        }
      })
      newValue.isInitializedParents = true
      set((state) => {
        return newValue
      })
      // parents?.forEach(() => {

      // })
    }
  }
  return state
})

export const useInitialized = () => {
  const store = useStore()
  useEffect(() => {
    store.initialize()
  }, [])
  return store.isInitialized
}

export const useCommunity = () => {
  const store = useStore()
  useEffect(() => {
    store.getCommunity()
  }, [])
  return store.community
}

export const useInitCommunityParents = () => {
  const params = useParams()
  const id = params.id as any as number
  const store = useStore()
  useEffect(() => {
    store.initCommunityParents(id)
  }, [id])
  return store.isInitializedParents
}

useStore.subscribe((data) => {
  console.log('global store changed, ', data)
})
