import React from 'react'
import { useNavigate } from 'react-router-dom'
import { useCommunity2List, useEventCallback, useStore } from '@/hooks'
import styles from './community2-list.component.sass'

type Props = {
  // data: ItemProps[]
}

type ItemProps<T> = {
  name: string
  id: number
  data: T
  onClick(data: T): void
}

const Community2Item: React.FC<ItemProps<Community2>> = React.memo((props) => {
  const onClick = useEventCallback(() => {
    props.onClick(props.data)
  })
  return (
    <div className={styles['item']} onClick={onClick}>
      <div className={styles['community2-icon']}></div>
      <div className={styles['name']}>{props.name}</div>
      <div className={styles['right-icon']}></div>
    </div>
  )
})

export const Community2List: React.FC<Props> = React.memo((props) => {
  const data = useCommunity2List()
  const navigate = useNavigate()

  const store = useStore()
  const items = data?.map((item) => {
    const onClick = () => {
      store.setCommunity2(item)
      navigate('/2/' + item.id)
    }
    return (
      <Community2Item
        onClick={onClick}
        data={item}
        key={item.id}
        name={item.name}
        id={item.id}
      />
    )
  })
  return <div className={styles['wrapper']}>{items}</div>
})
