import create from 'zustand'
import type { FormInstance } from 'antd-mobile/es/components/form'
import { useEffect } from 'react'
import { formatFormData, getDefaultUserInfo } from './use-user-form-store'
import { CommunityUserData } from '@/types'

export interface FormsState {
  forms: Array<FormInstance>
  add(form: FormInstance): void
  delete(form: FormInstance): void
  getForms(): FormInstance[]
  getData(roomId: number): Promise<CommunityUserData[]>
  getIdCards(): string[]
}

function extend<T>(o1: T, o2: any): T {
  return Object.keys(o1).reduce((pre, key) => {
    if (key in o2 && o2[key] != undefined) {
      pre[key] = o2[key]
    } else {
      pre[key] = (o1 as any)[key]
    }
    return pre
  }, Object.create({}))
}

export const useFormsStore = create<FormsState>((_set, _get) => {
  const state: FormsState = {
    forms: [],
    add(form) {
      if (!form) {
        return
      }
      _set((state) => {
        if (state.forms.indexOf(form) === -1) {
          return {
            forms: [...state.forms, form]
          }
        }
        return state
      })
    },
    delete(form) {
      _set((state) => {
        const forms = [...state.forms]
        if (forms.indexOf(form) !== -1) {
          forms.splice(state.forms.indexOf(form), 1)
          return {
            forms
          }
        }
        return state
      })
    },
    getForms() {
      return this.forms
    },

    async getData(roomId?: number) {
      const promises = this.forms.map(async (form) => {
        await form.validateFields()
        const res = extend(getDefaultUserInfo(), {
          ...formatFormData(form.getFieldsValue()),
          addressId: roomId
        })
        if (res.id <= 0) {
          // eslint-disable-next-line prettier/prettier
          delete (res as any).id
          // (res as any).id = null
        }
        return res
      })
      return await Promise.all(promises)
    },

    getIdCards() {
      return this.forms.map((form) => {
        return form.getFieldValue('cardNumber')
      })
    }
  }
  return state
})

export const useForms = (form: FormInstance) => {
  const store = useFormsStore()
  useEffect(() => {
    store.add(form)
    return () => {
      store.delete(form)
    }
  }, [form])
  return store
}
