import { FormsState } from '@/hooks'
import { FormInstance } from 'antd-mobile/es/components/form'
import type { Rule } from 'rc-field-form/lib/interface'
export enum CommunityType {
  city = 1,
  rural = 2
}

export interface CommunityUserData {
  addressId: number
  age: number | string
  cardNumber: string
  creator: string
  educationDegree: string
  gmtCreate: string
  gmtModified: string
  householderRelation: string
  id: number
  isDeleted?: boolean
  isDevelopmentArea: boolean
  isDisability: boolean
  isNeedyHouseholds: boolean
  isOverseasChinese: boolean
  isPersonnel: boolean
  isRetiredCadre: boolean
  maritalStatus: string
  militaryService: string
  mobile: string
  nation: string
  personName: string
  politicalOutlook: string
  profession: string
  propertyProperties: string
  registryAddress: string
  remarks: string
  sex: string
}
// | 'educationDegree'
// | 'householderRelation'
// | 'maritalStatus'
// | 'nation'
// | 'politicalOutlook'
// | 'sex'
// | 'propertyProperties'

type PickerTypeKeys =
  | 'nation'
  | 'politicalOutlook'
  | 'householderRelation'
  | 'militaryService'
  | 'profession'
  | 'educationDegree'
  | 'maritalStatus'

export type FormattedCommunityUserData = Omit<
  CommunityUserData,
  PickerTypeKeys
> & {
  nation: string[]
  politicalOutlook: string[]
  householderRelation: string[]
  militaryService: string[]
  profession: string[]
  educationDegree: string[]
  maritalStatus: string[]
}

export type UserBasicInfoKeys = keyof Pick<
  CommunityUserData,
  | 'personName'
  | 'cardNumber'
  | 'mobile'
  | 'sex'
  | 'nation'
  | 'age'
  | 'politicalOutlook'
  | 'propertyProperties'
  | 'householderRelation'
>

export type UserOtherInfoKeys = keyof Pick<
  CommunityUserData,
  | 'militaryService'
  | 'isDisability'
  | 'isRetiredCadre'
  | 'isNeedyHouseholds'
  | 'registryAddress'
  | 'profession'
  | 'isPersonnel'
  | 'isOverseasChinese'
  | 'educationDegree'
  | 'maritalStatus'
  | 'isDevelopmentArea'
  | 'remarks'
>

export type UserInfoKeys = UserBasicInfoKeys | UserOtherInfoKeys

/**
 *  options key
 */
export type SelectTypeKeys = keyof Pick<
  CommunityUserData,
  | 'educationDegree'
  | 'householderRelation'
  | 'maritalStatus'
  | 'nation'
  | 'politicalOutlook'
  | 'sex'
  | 'propertyProperties'
  | 'militaryService'
  | 'profession'
  | 'isDevelopmentArea'
  | 'isNeedyHouseholds'
>

/**
 *  select options code
 */
export type SelectTypeCode =
  | 'EDUCATION'
  | 'KFHOUSEHOLDERRELATIONSHIP'
  | 'MARITAL'
  | 'NATION'
  // 
  | 'POLITICALOUTLOOK'
  | 'SEX'
  //
  | 'RETYPE'
  | 'MILITIRY'

export type InputType = 'text' | 'number' | 'select' | 'picker' | 'textarea'

/**
 *  
 */
export type UserInfoItemConfig<T = any, V = any> = {
  inputType: InputType
  selectCode?: SelectTypeCode
  key: string
  isRequired: boolean
  name: string
  defaultValue: V
  options?: T[]
  rules:
    | Rule[]
    | ((form: FormInstance, index: number, store: FormsState) => Rule[])
  onBlur?: (value: V, form: FormInstance) => void
}

/**
 * 
 */
export type AsyncOption = {
  code: string
  dickey: string
  dicvalue: string
}

export enum CommunityLevel {
  /**
   * 
   */
  community = 1,
  /**
   * 
   */
  community2 = 2,
  /**
   * 
   */
  building = 3,
  /**
   * 
   */
  room = 5
}
