import { service } from '@/services'
import { useMutation, useQuery } from 'react-query'
import { useState, useEffect, useMemo } from 'react'
import { useCommunity2List } from './use-community2-list'
import { CommunityLevel } from '@/types'

export type DropdownItem = {
  label: string
  value: string | number
  key: string | number
  name: string
  id: string | number
  collectionStatus?: boolean
}
type State = {
  community2?: DropdownItem
  community2s?: DropdownItem[]
  building?: DropdownItem
  buildings?: DropdownItem[]
  room?: DropdownItem
  rooms?: DropdownItem[]
  isCommunity2ChildAsRoom?: boolean
}

type Handlers = {
  // setCommunity(id: number): void
  setCommunity2(id: number): void
  setBuilding(id: number): void
  setRoom(id: number): void
  getRooms(params: { id: number; name?: string }): void
  // getCommunity2s(id: number): Promise<void>;
  // getBuildings(id: number): Promise<void>;
  // getRooms(id: number): Promise<void>;
}

export const useCommunityOptions = () => {
  const community2List = useCommunity2List()
  const [state, setState] = useState<State>({})
  const community2s = useMemo(() => {
    return community2List?.map((item) => {
      return {
        ...item,
        label: item.name,
        value: item.id,
        key: item.id
      }
    })
  }, [community2List])
  // const { data: community2s } = useQuery(
  //   ['dynamicCommunity2s', store.community?.id],
  //   () => {
  //     if (store.community2List?.length) {
  //       return Promise.resolve(store.community2List as any[])
  //     }
  //     if (store.community?.addressType === 1) {
  //       return service.getCommunityOptions({
  //         id: Number(store.community?.id)
  //       })
  //     } else {
  //       return []
  //     }
  //   }
  // )

  const { data: buildings } = useQuery(
    ['dynamicBuildings', state.community2?.value],
    () => {
      if (state.community2?.value === undefined) {
        return []
      }
      return service.getCommunityOptions({
        id: Number(state.community2?.value)
      })
    }
  )

  useEffect(() => {
    setState((s) => {
      const isCommunity2ChildAsRoom = buildings?.some(
        (item) => item.level == CommunityLevel.room
      )
      return {
        ...s,
        isCommunity2ChildAsRoom
      }
    })
  }, [buildings])

  const { data: rooms, mutate: getRooms } = useMutation(
    (params: { id: number; name?: string }) => {
      return service.getCommunityOptions(params)
    }
  )
  // const { data: rooms } = useQuery(
  //   ['dynamicRooms', state.building?.value],
  //   () => {
  //     if (community?.addressType === 2) {
  //       return service.getCommunityOptions({
  //         id: Number(community?.id)
  //       })
  //     } else {
  //       if (state.building?.value === undefined) {
  //         return []
  //       }
  //       return service.getCommunityOptions({
  //         id: Number(state.building?.value)
  //       })
  //     }
  //   }
  // )

  const lists = useMemo(() => {
    return {
      community2s,
      buildings,
      rooms
    }
  }, [community2s, buildings, rooms])

  useEffect(() => {
    setState((s) => {
      return {
        ...s,
        ...lists
      }
    })
  }, [lists])

  const handlers: Handlers = {
    setCommunity2(id: number) {
      setState((state) => {
        const data = state.community2s?.find((item) => item.value == `${id}`)
        return {
          ...state,
          community2: data || state.community2,
          building: undefined,
          room: undefined
        }
      })
    },

    setBuilding(id: number) {
      setState((state) => {
        const data = state.buildings?.find((item) => item.value == `${id}`)
        return {
          ...state,
          building: data || state.building,
          room: undefined
        }
      })
    },

    setRoom(id: number) {
      setState((state) => {
        const data = state.rooms?.find((item) => item.value == `${id}`)
        return {
          ...state,
          room: data || state.room
        }
      })
    },
    getRooms
  }

  return [state, handlers] as const
}
