import { useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { useStore } from './use-store'

export const useRooms = () => {
  const store = useStore()
  const { rooms } = store
  const { id } = useParams()
  useEffect(() => {
    store.getRooms({ id: Number(id) })
    return () => {
      store.resetRooms()
    }
  }, [id])
  return rooms
}
